import { SET_TOKEN, SET_USER_DATA } from './actionTypes';

const initialState = {
  token: ''
};
export default function todos(state = initialState, action) {
  switch (action.type) {
    case SET_TOKEN:
      return {
        ...state,
        token: action.token
      };
    case SET_USER_DATA:
      return {
        ...state,
        userData: action.userData
      };

    default:
      return state;
  }
}
