import React, { Component } from 'react';
import { connect } from 'react-redux';
import './App.css';
import store from './redux/store';
import { Provider } from 'react-redux';
import { setToken, setUserData } from './redux/Auth/actions';
import Main from './containers/main';

class SideEffects extends Component {
  componentDidMount() {
    if (window.location.search.includes('access_token')) {
      const token = window.location.search.slice(1);
      this.props.setToken(token);
      fetch(`https://api.github.com/user?${token}`)
        .then(function(response) {
          return response.json();
        })
        .then(function(myJson) {
          console.log({ myJson });
          this.props.setUserData(myJson);
        });
    }
  }
  render() {
    return <Main />;
  }
}

const ConnectedApp = connect(
  state => ({ auth: state.auth }),
  { setToken, setUserData }
)(SideEffects);

class App extends Component {
  render() {
    return (
      <div className="App">
        <Provider store={store}>
          <ConnectedApp />
        </Provider>
      </div>
    );
  }
}

export default App;
